.background {
  width: 100vw;
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.doneDialog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
  box-shadow: 0 6px 100px -4px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.title {
  color: #333;
  font-family: Roboto;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  line-height: 33px;
  padding: 20px 100px;
}
